import React, { useEffect, useState } from "react";
import Axios from "axios";
import { API_KEY } from "../App";
import styled from "styled-components";

const Container = styled.div`
  display: flex;
  flex-direction: row;
  padding: 20px 30px;
  justify-content: center;
  border-bottom: 1px solid lightgray;
`;

const MovieInfoComponent = (props) => {
  const [movieInfo, setMovieInfo] = useState();
  const { selectedMovie } = props;

  useEffect(() => {
    Axios.get(
      `https://www.omdbapi.com/?i=${selectedMovie}&apikey=${API_KEY}`,
    ).then((response) => setMovieInfo(response.data));
  }, [selectedMovie]);
  return (
    <Container>
      {movieInfo ? (
        <>
                <div className="card mb-3" >
                <div className="row g-0">
                    <div className="col-md-4">
                    <img src={movieInfo?.Poster} className="img-fluid rounded-start" alt={movieInfo?.Title} />
                    </div>
                    <div className="col-md-8">
                    <div className="card-body">
                        <h5 className="card-title">{movieInfo?.Title}</h5>
                        <p className="card-text">{movieInfo?.Plot}</p>
                        <p className="card-text"><small className="text-muted">{movieInfo?.Actors}</small></p>
                        <p className="card-text"><small className="text-muted">{movieInfo?.Director}</small></p>
                        <p className="card-text"><small className="text-muted">{movieInfo?.Language}</small></p>
                        <p className="card-text"><small className="text-muted">{movieInfo?.Released}</small></p>
                        <p className="card-text"><small className="text-muted">{movieInfo?.Genre}</small></p>
                        <p className="card-text"><small className="text-muted">{movieInfo?.Runtime}</small></p>
                        <p className="card-text"><small className="text-muted">{movieInfo?.imdbRating}</small></p>
                    </div>
                    </div>
                </div>
                </div>
        </>
      ) : (
        "Loading..."
      )}
    </Container>
  );
};
export default MovieInfoComponent;